import "./card.css";

// personalInfoList.js

function Card(title, items) {
  // Create the main container
  const container = document.createElement("div");
  container.classList.add("personal-info-list");

  // Create the title
  const titleElement = document.createElement("h3");
  titleElement.textContent = title;
  container.appendChild(titleElement);

  // Create the list
  const list = document.createElement("ul");
  items.forEach((item) => {
    const listItem = document.createElement("li");
    listItem.classList.add("personal-info-item");

    // Create the icon element
    const iconElement = document.createElement("span");
    iconElement.classList.add("icon");
    iconElement.innerHTML = item.icon; // Assumes `icon` is an HTML string

    // Create the label element
    const labelElement = document.createElement("span");
    labelElement.textContent = item.label;

    // Append icon and label to the list item
    listItem.appendChild(iconElement);
    listItem.appendChild(labelElement);

    // Append list item to the list
    list.appendChild(listItem);
  });

  // Append the list to the container
  container.appendChild(list);

  return container;
}

// Usage example
const personalItems = [
  { icon: '<i class="fas fa-user"></i>', label: "Profile" }, // Font Awesome icon as HTML string
  { icon: '<i class="fas fa-lock"></i>', label: "Password" },
  { icon: '<i class="fas fa-shield-alt"></i>', label: "Data" },
];

document.body.appendChild(Card("Personal", personalItems));
